export default {
	onLoad() {
		let userId = uni.getStorageSync('user_id');
		this.id = userId
		this.getdata()
	},
	data() {
		return {
			modalName: null,
			inputvalue: '',
			tagList: [],
			baseUrl: "http://47.104.29.236:9999/api/v1",
			// 用户id
			id: '',
			// 选中tag标签数组
			our_tag: [],
			num: 0
		}
	},
	// 离开页面的时候触发
	onUnload() {
		this.our_tag = []
		this.tagList.forEach((v, i) => v.checked == true ? this.our_tag.push(v) : [])
		uni.setStorageSync('ourMusic', JSON.stringify(this.our_tag))
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		// 点击保存创建
		saveModal() {
			this.tagList.push({
				id: this.tagList.length,
				tag_name: this.inputvalue,
				checked: false
			})
			uni.request({
				url: this.baseUrl + `/add_foods_tag/${this.id}`,
				method: "POST",
				header: {
					"content-type": "application/x-www-form-urlencoded"
				},
				data: {
					tags: JSON.stringify(this.tagList),
					id: this.id
				},
				success: (res) => {
					if (res.data.ok == 1) {
						uni.showToast({
							title: "添加成功"
						})
					}
				},
				fail: (error) => {
					console.log(error)
				}
			})
			this.inputvalue = ''
			return this.modalName = null
		},
		getdata() {
			uni.request({
				url: this.baseUrl + `/our_music/${this.id}`,
				method: 'GET',
				success: (res) => {
					if (res.data.ok !== 1) {
						uni.showToast({
							title: "请求失败",
							image: '../../static/toast/error.png'
						})
					}
					const data = res.data.data.tag_music_name.split(',')
					const taglist = data
					taglist.forEach((v, i) => {
						this.tagList.push({
							id: i,
							tag_name: v,
							checked: false
						})
					})
					uni.getStorage({
						key: 'ourMusic',
						success: (res) => {
							this.our_tag = JSON.parse(res.data)
							this.our_tag.forEach(v => {
								let check = this.tagList.findIndex(i => v.id == i.id)
								this.tagList[check].checked = true
							})
						}
					})
				},
				fail(error) {
					console.log(error)
				}
			})
		},
		// 选中框
		CheckboxChange(index) {
			this.tagList[index].checked = !this.tagList[index].checked
		},
	}
}
